"use client";
import Image from "next/image";
import { useEffect, useMemo, useState } from "react";
import { Dancing_Script } from "next/font/google";
import MainLayout from "../../components/sections/MainLayout";
import SocialButton from "../../components/ui/SocialButton";
import { socialLinks } from "@/constants/socialLinks";
import { SocialLink } from "@/types/socialLink";

const dancing = Dancing_Script({
  subsets: ["latin"],
  weight: ["600", "700"],
});

export default function Home() {
  const [links] = useState<SocialLink[]>(socialLinks);
  const heroImages = useMemo(
    () => ["/home/me3.png", "/about/me2.png", "/about/me3.png", "/about/me4.png"],
    []
  );
  const heroCaptions = useMemo(
    () => [
      "At Tech Conference",
      "Hackathon Win",
      "Team Demo Day",
      "Campus Project Build",
    ],
    []
  );
  const [slideIndex, setSlideIndex] = useState(0);

  useEffect(() => {
    if (heroImages.length <= 1) return;
    const timer = setInterval(() => {
      setSlideIndex((prev) => (prev + 1) % heroImages.length);
    }, 3000);
    return () => clearInterval(timer);
  }, [heroImages]);

  return (
    <MainLayout>
      <div
        className="relative flex flex-col-reverse md:flex-row items-center md:items-center justify-between gap-10 md:gap-14 py-10 px-4 sm:px-8 md:px-10 rounded-3xl bg-white shadow-[0_8px_30px_-18px_rgba(0,0,0,0.15)] max-w-7xl w-full mx-auto"
      >
        <div className="flex-1 md:basis-3/5 flex flex-col gap-5 max-w-3xl text-center md:text-left">
          <h1
            className={`${dancing.className} fade-up text-[52px] sm:text-[60px] md:text-[72px] font-bold tracking-tight`}
            style={{
              animationDelay: "200ms",
              background: "linear-gradient(135deg, #1e40af, #7c3aed)",
              WebkitBackgroundClip: "text",
              color: "transparent",
              textShadow: "0 2px 20px rgba(102,126,234,0.2)",
            }}
          >
            Daman Kumar
          </h1>

          <p
            className="fade-up typing text-[20px] sm:text-[22px] font-semibold"
            style={{ color: "#64748B", animationDelay: "400ms" }}
          >
            Software Engineering Student
          </p>

          <p
            className={`${dancing.className} fade-up text-base sm:text-lg leading-relaxed`}
            style={{ color: "#475569", animationDelay: "600ms" }}
          >
            Learning. Building. Solving problems.
          </p>

          <div className="flex flex-wrap gap-4 items-center justify-center md:justify-start mt-3">
            <a
              href="/projects"
              className="mt-3 inline-flex items-center gap-3 rounded-[12px] bg-gradient-to-r from-[#667EEA] to-[#764BA2] px-10 py-4 text-[18px] font-semibold text-white shadow-[0_10px_30px_rgba(102,126,234,0.4)] transition-all duration-200 hover:-translate-y-[3px] hover:shadow-[0_15px_40px_rgba(102,126,234,0.5)] hover:from-[#6f7ef3] hover:to-[#8150b0] bounce-in"
              style={{ animationDelay: "1100ms" }}
            >
              View My Work
              <span aria-hidden="true">→</span>
            </a>
            <a
              href="/resume.pdf"
              className="mt-3 inline-flex items-center gap-3 rounded-[12px] border-2 border-[#667EEA] px-10 py-4 text-[18px] font-semibold text-[#667EEA] shadow-[0_8px_22px_-14px_rgba(102,126,234,0.35)] transition-all duration-200 hover:-translate-y-[3px] hover:shadow-[0_12px_28px_-12px_rgba(102,126,234,0.4)] hover:bg-[#eef2ff] bounce-in"
              style={{ animationDelay: "1150ms" }}
            >
              Download Resume
              <span aria-hidden="true">↓</span>
            </a>
          </div>

          <div className="flex gap-6 items-center justify-center md:justify-start mt-4">
            {links.map((link, index) => (
              <SocialButton
                key={index}
                icon={link.icon}
                text={link.text}
                href={link.href}
                onClick={link.onClick}
                ariaLabel={link.ariaLabel}
                classNameOverride={`fade-up`}
                styleOverride={{ animationDelay: `${800 + index * 100}ms` }}
              />
            ))}
          </div>
        </div>

        <div className="flex-1 md:basis-2/5 relative w-full max-w-[380px] sm:max-w-[460px] md:max-w-[500px] aspect-[4/5] shrink-0 fade-scale" style={{ animationDelay: "500ms" }}>
          <div className="absolute inset-[18px] sm:inset-[24px] blur-[70px] sm:blur-[90px] rounded-[36%] bg-gradient-to-br from-[#667eea]/40 to-[#764ba2]/35 opacity-70 animate-pulse" aria-hidden="true" />
          <div className="relative w-full h-full overflow-hidden rounded-2xl border-4 border-white shadow-[0_24px_70px_rgba(0,0,0,0.18)] ring-[10px] ring-[rgba(102,126,234,0.12)] bg-white">
            {heroImages.map((src, idx) => {
              const isActive = idx === slideIndex;
              return (
                <div
                  key={src}
                  className={`absolute inset-0 transition-all duration-800 ease-in-out ${
                    isActive ? "opacity-100 translate-x-0" : "opacity-0 translate-x-4"
                  }`}
                >
                  <Image
                    src={src}
                    alt="Portrait of Daman Kumar"
                    fill
                    priority={idx === 0}
                    className="object-cover transition-transform duration-[1800ms] ease-out"
                    style={{
                      transform: isActive ? "scale(1.06) translateY(-4px)" : "scale(1.01)",
                    }}
                    sizes="100vw"
                  />
                  <div className="absolute inset-0 bg-gradient-to-t from-slate-900/35 via-slate-900/10 to-transparent pointer-events-none" />
                  <div className="absolute top-3 left-3 inline-flex items-center gap-2 rounded-full bg-white/95 px-3 py-1 text-xs font-semibold text-slate-800 shadow-[0_10px_24px_-14px_rgba(0,0,0,0.25)]">
                    {idx + 1}/{heroImages.length} · {heroCaptions[idx] || "Capture"}
                  </div>
                  <div className="absolute bottom-3 left-3 right-3 flex items-center justify-between rounded-full bg-white/92 px-3 py-2 text-xs font-semibold text-slate-800 shadow-[0_10px_22px_-16px_rgba(0,0,0,0.35)]">
                    <span>{heroCaptions[idx] || "Behind the scenes"}</span>
                    <span aria-hidden="true">↗</span>
                  </div>
                </div>
              );
            })}
          </div>
          <div className="mt-4 flex items-center justify-center gap-2">
            {heroImages.map((_, idx) => {
              const isActive = idx === slideIndex;
              return (
                <button
                  key={idx}
                  onClick={() => setSlideIndex(idx)}
                  aria-label={`Go to slide ${idx + 1}`}
                  className={`h-2.5 w-2.5 rounded-full border transition-all duration-200 ${
                    isActive
                      ? "bg-gradient-to-r from-[#667eea] to-[#764ba2] border-transparent scale-110"
                      : "bg-transparent border-slate-300 hover:border-[#667eea]"
                  }`}
                />
              );
            })}
          </div>
        </div>
      </div>
    </MainLayout>
  );
}

